@import url(https://fonts.googleapis.com/css?family=Montserrat:300,400,500&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box;
  line-height: 1.5em;
}

body {
  font: 400 16px 'Montserrat', sans-serif;
  background: #024BFF;
  color: #fff;
  -webkit-font-smoothing: antialiased;
}

input, button, textarea {
  font: 400 15px 'Montserrat', sans-serif;
  vertical-align: middle;
}

button {
  cursor: pointer;
}

a {
  text-decoration: none;
}
.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  width: 1200px;
  height: 100vh;
}

.container .text-content {
  text-align: center;
  flex-grow: 1;
}

.container .text-content .title {
  font-size: 30px;
  padding: 5px 0 50px 0;
}

.container .text-content .common-text {
  font-size: 20px;
  font-weight: 300;
}

.container .social-media-pills {
  flex-grow: 1;
  display: inline-flex;
}

.container .social-media-pills .pill {
  background-color: #fff;
  height: 50px;
  width: 50px;
  border: 5px solid #F78372;
  border-radius: 50px;
  text-align: center;
  line-height: 45px;
  font-size: 22px;
  color: #0000CD;
  margin: 20px
}
